/* JobGallery.css */

.job-gallery-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.job-gallery-search-bar {
  margin-bottom: 20px;
}

.job-gallery-search-bar input[type="text"] {
  padding: 10px;
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
}

.job-gallery-filters {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  flex-wrap: wrap;
}

.job-gallery-filters select,
.job-gallery-filters input {
  padding: 10px; /* Consistent padding */
  border-radius: 4px;
  border: 1px solid #ccc;
  font-size: 16px;
}

.job-gallery-job-cards {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 20px;
}

.job-gallery-job-card {
  background: #f9f9f9;
  min-width: 350px;
  color: #333;
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
  width: calc(33.33% - 40px); /* Adjust based on your requirement */
  transition: transform 0.3s ease-in-out;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  position: relative;
  border-top: 5px solid #8c36d8;
}

.job-gallery-job-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2);
}
