.job-gallery-job-card button {
  margin-top: 20px;
  padding: 12px 20px;
  background-color: #8c36d8;
  color: #fff;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.job-gallery-job-card button:hover {
  background-color: #6a2472;
}
