/* Main container styles */
.job-data-main {
    display: flex;
    justify-content: space-between;
    margin-top: 100px;
    margin-bottom: 100px;
    
  }
  
  /* Job Description container styles */
  .job-description-container {
    flex: 1;
    max-width: 60%;
    margin: 0 auto;
    padding: 20px;
  
    border-radius: 8px;
  
    display: none;
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;
    
  }
  
  .show-job-description {
    display: block;
    opacity: 1;
    transform: translateY(0);
  }
  
  /* Left side of Job Description */
  .job-description-left {
    flex: 1;
    transition: transform 0.5s ease-in-out;
  }
  
  .job-title {
    font-size: 28px;
    color: #1c1e21;
    margin-bottom: 10px;
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;
  }
  
  .show-job-description .job-description-left .job-title {
    opacity: 1;
    transform: translateY(0);
  }
  
  .job-info {
    display: flex;
    align-items: baseline;
    margin-top: 10px;
  }
  
  .job-info-icon {
    margin-right: 10px;
    font-size: 1.4em;
    color: #8c36d8;
  }
  
  /* Job Information details */
  .job-location,
  .job-salary,
  .job-type,
  .job-company-info {
    margin-top: 15px;
    color: #1c1e21;
  }
  
  /* Job Description sections */
  .job-about,
  .job-responsibilities,
  .job-requirements,
  .job-equal-opportunity {
    margin-top: 20px;
    color: #1c1e21;
  }
  
  .job-description-text,
  .job-equal-opportunity p {
    line-height: 1.6;
  }
  
  .job-responsibilities ul,
  .job-requirements ul {
    list-style-type: none;
    padding: 0;
  }
  
  .job-responsibilities li,
  .job-requirements li {
    margin-bottom: 10px;
  }
  
  .jd-tick-icon {
    margin-right: 10px;
    color: #8c36d8;
  }
  
  /* Apply Now Button styles */
  .apply-now-button {
    margin-top: 20px;
  }
  
  .apply-button {
    background-color: #8c36d8;
    color: #fff;
    padding: 12px 24px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    display: flex;
    align-items: center;
  }
  
  .apply-icon {
    margin-left: 5px;
  }
  
  .apply-button:hover {
    background-color: #6e2c9c;
  }
  
  /* Additional styling for each job description */
  .job-description-left + .job-description-left {
    margin-top: 40px;
  }
  
  /* Job Cards container styles */
  .jobData-gallery-main {
    max-width: 30%;
    display: flex;
    flex-wrap: wrap;
    padding: 20px;
  }
  .jobData-gallery-main h2 {
    font-size: 1.6rem;
    color: #1c1e21;
    width: 75%;
    text-align: center;
  }
  
  /* Job Card styles */
  .jobData-gallery-job-card {
    background: white;
    color: black;
    padding: 20px;
    border-radius: 12px;
    margin: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    width: 300px;
    opacity: 1;
    transform: translateY(0);
    transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    position: relative;
    border-top: 5px solid #8c36d8;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    cursor: pointer;
  }
  
  .jobData-gallery-job-card:hover {
    transform: scale(1.05);
  }
  
  /* Job Card Title and Icon container styles */
  .jobData-gallery-titleicon-container {
    margin-bottom: 12px;
  }
  
  /* Job Card Type container styles */
  .jobData-gallery-type-container {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: #8c36d8;
    color: white;
    padding: 5px;
    border-radius: 8px;
  }
  
  .jobData-gallery-type-container p {
    margin: 0;
  }
  
  /* Job Card Location container styles */
  .jobData-gallery-location-container {
    display: flex;
    align-items: center;
    justify-content: center;
    color: #8c36d8;
    margin-top: 10px;
  }
  
  .jobData-gallery-location-container p {
    margin: 0;
    margin-left: 5px;
  }
  
  
  .jobData-gallery-job-card h3 {
    font-size: 20px;
    margin-bottom: 10px;
  }
  
  
  .jobData-gallery-job-card p {
    margin: 0;
  }
  