/* JobsDescriptionText.css */

.job-data-main {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 20px;
    background-color: #f5f5f5;
    border-radius: 8px;
  }
  
  .job-description-left {
    flex: 1;
  }
  
  .job-title {
    font-size: 28px;
    margin-bottom: 15px;
  }
  
  .job-info {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }
  
  .job-info-icon {
    font-size: 18px;
    margin-right: 10px;
  }
  
  .job-about,
  .job-responsibilities,
  .job-requirements,
  .job-equal-opportunity {
    margin-bottom: 20px;
  }
  
  .job-about h2,
  .job-responsibilities h2,
  .job-requirements h2 {
    font-size: 22px;
    margin-bottom: 10px;
  }
  
  .job-description-text {
    line-height: 1.5;
  }
  
  ul {
    list-style: none;
    padding: 0;
  }
  
  .jd-tick-icon {
    margin-right: 8px;
  }
  
  .apply-now-button {
    text-align: right;
  }
  
  .apply-button {
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    background-color: #007bff;
    color: #fff;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .apply-button:hover {
    background-color: #0056b3;
  }
  

