/* AuthButton.css */

.AuthButton-main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
  gap: 20px; /* Add some space between the message and buttons */
}

.backend-message {
  font-size: 16px;
  color: #ff0000; /* Red color for error messages, adjust as needed */
  margin-bottom: 10px;
}

.AuthButton {
  display: flex;
  align-items: center;
  padding: 15px 25px;
  font-size: 18px;
  font-weight: bold;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.2s ease, box-shadow 0.2s ease;
}

.GoogleSignInButton {
  background: linear-gradient(to right, #8c36d8, #3b14a3);
  color: #ffffff;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.1);
}

.GoogleSignInButton:hover {
  background: linear-gradient(to right, #7e17d8, #2e0e89);
  transform: scale(1.05);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
}

.GoogleConnectButton {
  background: linear-gradient(to left, #8c36d8, #3b14a3);
  color: #ffffff;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.1);
}

.GoogleConnectButton:hover {
  background: linear-gradient(to right, #7e17d8, #2e0e89);
  transform: scale(1.05);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
}

.AuthButton-google-icon {
  margin-right: 12px;
  font-size: 24px;
}

/* Optional: Add a subtle animation on hover for the icon */
.AuthButton:hover .AuthButton-google-icon {
  animation: bounce 0.3s ease;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-8px);
  }
  60% {
    transform: translateY(-4px);
  }
}

.AuthButton-or {
  font-size: 18px;
  color: #777;
  margin: 0 10px;
}
