/* UserProfile.css */

.user-profile-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin-top: 100px;
  margin-bottom: 100px;
  position: relative;
}

.profile-picture-container {
  margin-bottom: 20px;
  position: relative;
}

.profile-picture {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
}

.edit-profile-label {
  position: absolute;
  top: 5px;
  right: 5px;
  cursor: pointer;
}

.edit-icon {
  color: #8c36d8;
}

.profile-file-input {
  display: none;
}

.user-details-container {
  width: 100%;
}

.profileform {
  display: flex;
  flex-direction: column;
}

.profile-form-group {
  width: 100%;
  margin-bottom: 15px;
}

.profile-field-name {
  font-weight: bold;
  margin-bottom: 5px;
}

.profile-field-value {
  padding: 10px;
  font-size: 14px;
  border: 1px solid #ddd;
  border-radius: 10px;
  width: 100%;
}

.profile-profile-file-input-container {
  display: flex;
  align-items: center;
}

.profile-resume-label {
  padding: 8px;
  font-size: 14px;
  background-color: #8c36d8;
  color: #fff;
  border-radius: 4px;
  cursor: pointer;
  margin-right: 10px;
}

.profile-selected-file {
  padding: 8px;
  font-size: 14px;
  margin-top: 5px;
  color: #333;
}

.profile-resume-link {
  display: block;
  margin-top: 10px;
  color: #8c36d8;
  text-decoration: none;
  background-color: transparent;
  border: none;
}

.profile-resume-link:hover {
  text-decoration: underline;
}

.profile-education-section,
.profile-workexperience-section {
  margin-top: 20px;
  text-align: left;
}

.profile-education-details,
.profile-experience-details {
  margin-bottom: 20px;
}

.add-profile-education-button,
.delete-profile-experience-button,
.save-profile-education-button,
.add-profile-experience-button,
.delete-profile-education-button,
.save-profile-experience-button {
  background-color: #8c36d8;
  color: #fff;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

.add-profile-education-button:hover,
.save-profile-education-button:hover,
.add-profile-experience-button:hover,
.delete-profile-education-button:hover,
.delete-profile-experience-button:hover,
.save-profile-experience-button:hover {
  background-color: #6a2494;
}

.checkbox-group {
  display: flex;
  align-items: center;
}

.profile-checkbox-container {
  display: flex;
  align-items: center;
  margin-left: 10px;
}

.profile-checkbox-label {
  margin-left: 5px;
}


.profile-field-value:hover {
  border-color: #8c36d8;
}


.profile-field-value:focus {
  border-color: #8c36d8; 
  outline: none; 
}

